<script>
  export default {
    created(){
      particlesJS.load('particles-js', 'static/js/particlesjs-config.json', function() {
        console.log('callback - particles.js config loaded');
      });
    },
    data() {
      var checkAge = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('年龄不能为空'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
            if (value < 18) {
              callback(new Error('必须年满18岁'));
            } else {
              callback();
            }
          }
        }, 1000);
      };
	  var checkName = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('姓名不能为空'));
        }
		else {
			callback();
		}
      };
	  var checkStudentid = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('学号不能为空'));
        }
		else {
			callback();
		}
      };
	  var checkCollege = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('二级学院不能为空'));
        }
		else {
			callback();
		}
      };
	  var checkGrades = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('班级不能为空'));
        }
		else {
			callback();
		}
      };
	  var checkPhone = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('联系方式不能为空'));
        }
		else {
			callback();
		}
      };
      return {
        error: '',
        show: true,
        ruleForm2: {
          age: '',
		  name: '',
		  studentid: '',
		  grades: '',
		  college: '',
		  phone: ''
        },
        rules2: {
			age: [
              { validator: checkAge, trigger: 'blur' }
  		  ],
          name: [
            { validator: checkName, trigger: 'blur' }
		  ],
		  studentid: [
            { validator: checkStudentid, trigger: 'blur' }
          ],
          college: [
            { validator: checkCollege, trigger: 'blur' }
          ],
		  grades: [
            { validator: checkGrades, trigger: 'blur' }
          ],
          phone: [
            { validator: checkPhone, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
       open3() {
        this.$notify({
          title: '成功',
          message: '恭喜报名成功',
          type: 'success'
        });
      },
      open4() {
        this.$notify({
          title: '抱歉',
          message: this.error,
          type: 'warning'
        });
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
            let k;
          if (valid) {
			  axios.post('/api/sign', {
                   name : this.ruleForm2.name,
                   studentid: this.ruleForm2.studentid,
				   college : this.ruleForm2.college,
                   grades: this.ruleForm2.grades,
				   phone : this.ruleForm2.phone
               }).then(response=> {
                      k = response.data;
                      console.log(k)
                 if(k=="ok"){
                    // this.$router.push('/success');
                    this.show=false;
                    this.open3();
                }else{
                    console.log(k);
                    if(k.phone!=null){
                        this.error = "该手机号已存在";
                    }else if(k.studentid!=null){
                        this.error = "该学号已存在";
                    }else{
                         this.error = k;
                    }
                    // console.log(k.phone);
                    // console.log(this.error);
                    this.open4();
                    // this.error=response.data;
                }  
                   });
				   
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

